@font-face {
  font-family: "PTMono";
  src: url("../fonts/PTMono-Regular.woff2") format("woff2"), url("../fonts/PTMono-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "RobotoCondensed";
  src: url("../fonts/RobotoCondensed-Regular.woff2") format("woff2"), url("../fonts/RobotoCondensed-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "RobotoCondensed";
  src: url("../fonts/RobotoCondensed-Bold.woff2") format("woff2"), url("../fonts/RobotoCondensed-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
.html {
  height: 100%;
  font-family: "RobotoCondensed", "Arial", sans-serif;
  font-size: 14px;
  line-height: 16.41px;
  font-weight: 400;
  font-style: normal;
  scroll-behavior: smooth;
  margin: 0 auto;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.body {
  height: 100%;
  display: flex;
  flex-flow: column;
  margin: 0;
}

.main {
  flex: 1 0 auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  border: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
  padding: 0;
  margin: -1px;
}

a {
  text-decoration: none;
  color: rgb(0, 0, 0);
}

img {
  border-style: none;
}

button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
}

.preloader {
  position: fixed;
  inset: 0;
  z-index: 1001;
}

.preloader__container {
  position: relative;
  top: 50%;
  left: 50%;
  width: 70px;
  height: 70px;
  margin-top: -35px;
  margin-left: -35px;
  text-align: center;
  animation: preloader-rotate 2s infinite linear;
}

.preloader__item {
  position: absolute;
  display: inline-block;
  top: 0;
  background-color: skyblue;
  border-radius: 100%;
  width: 35px;
  height: 35px;
  animation: preloader-bounce 2s infinite ease-in-out;
}

.preloader__item:last-child {
  top: auto;
  bottom: 0;
  animation-delay: -1s;
}

@keyframes preloader-rotate {
  100% {
    transform: rotate(360deg);
  }
}
@keyframes preloader-bounce {
  0%, 100% {
    transform: scale(0);
  }
  50% {
    transform: scale(1);
  }
}
.header {
  display: flex;
  width: 100%;
  min-width: 1024px;
  justify-content: space-between;
  background-color: black;
  align-items: center;
}

.header__logo-wrapper {
  display: flex;
}

.header__img {
  width: 185px;
  height: 125px;
}

.header__block {
  display: flex;
  max-width: 140px;
  padding: 5px;
}

.header__block-img {
  width: 40px;
  height: 60px;
  margin: 10px;
}

.header__block-line-darck {
  height: 80px;
  width: 1px;
  border: solid 1px rgba(78, 74, 74, 0.74);
  margin-right: 5px;
}

.header__block-link {
  display: flex;
  flex-wrap: wrap;
  color: white;
}

.header__block-content {
  color: white;
}

.header__lang-and-slogan-wrapper {
  display: flex;
  justify-content: space-between;
  width: 600px;
}

.header__slogan-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
}

.header__line {
  width: 1px;
  height: 50px;
  border: solid 1px red;
  margin-right: 5px;
}

.header__content {
  font-size: 13px;
  color: white;
  max-width: 240px;
  text-transform: uppercase;
}

.header__block-lang {
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 700;
  height: 80px;
  margin: 25px 10px 0 0;
}

.header__lang {
  margin: 5px;
  font-size: 17px;
}

.header__lang-ru-content {
  color: white;
}

.header__lang-slash {
  color: white;
  font-size: 17px;
}

.footer {
  display: flex;
  justify-content: center;
  background-color: black;
  color: white;
  font-size: 6px;
}

.main__wrapper {
  display: grid;
  grid-template-columns: 1fr 5fr;
}

.main__navigation-wrapper {
  display: grid;
  grid-template-columns: 0.5fr 1fr;
}

.main__content-wrapper-news {
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.main__navigation-block {
  width: 250px;
}

.main__navigation-faster {
  display: flex;
}

.main__navigation-faster-list {
  width: 220px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  list-style-type: none;
}

.main__navigation-link-line {
  border: 1px solid black;
  height: 17px;
}

.main__navifation-main-block {
  color: red;
}

.main__navigation-list {
  margin: 0;
}

.main__navigation-element {
  display: flex;
  flex-wrap: wrap;
  width: 220px;
  justify-content: center;
}

.main__navigation-main-element {
  margin-bottom: 15px;
  font-size: small;
}

.main__navigation-main-content:hover {
  color: rgba(8, 8, 8, 0.774);
  transition: 0.3s;
  text-decoration: underline;
}

.main__navigation-element-link {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-bottom: 25px;
}

.main__navigation-element-img-content {
  width: 150px;
  font-weight: 550;
}

.main__round-date {
  display: flex;
  height: 150px;
}

.main__round-date-img {
  width: 1285px;
  height: 160px;
}

.main__news-link {
  font-weight: 600;
}
.main__news-link:hover {
  text-decoration: underline;
  color: #ffffff;
}

.main__title-block {
  margin-top: 20px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  background-color: red;
  height: 50px;
}

.main__title {
  font-size: 16px;
  color: white;
  margin-left: 10px;
}

.main__news-list {
  list-style-type: none;
  padding: 0;
}

.main__news-wrapper {
  list-style-type: none;
  padding: 0;
}

.main__news-date {
  color: #AAAAAA;
}

.main__news-lick {
  font-weight: 600;
}
.main__news-lick:hover {
  text-decoration: underline;
}

.main__news-title:hover {
  transition: 0.3s;
  color: rgba(0, 0, 0, 0.781);
}

.main__news-content {
  color: #333333;
}

.main__secondery-content-element {
  display: flex;
  flex-wrap: wrap;
  width: 200px;
}

.main__secondery-content-text {
  font-size: 12px;
}
.main__secondery-content-text:hover {
  text-decoration: underline;
}

.main__secondery-content-text-color {
  color: #AAAAAA;
}

.main__secondery-underline-none:hover {
  text-decoration: none;
}

.main__wrapper-naming-block {
  width: 100%;
}

.main__naming-block {
  display: flex;
  justify-content: space-around;
  align-items: center;
  height: 120px;
  background-color: red;
}

.main__main__naming-block-title {
  font-size: 24px;
  font-weight: 600;
  color: white;
}

.main__main__naming-block-content {
  color: white;
  width: 260px;
}

.main__wrapper-about-company {
  display: grid;
  grid-template-columns: 2fr 1fr;
}

.main__content-bolt {
  font-weight: 600;
}

.main__mini-navigation-block {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.main__mini-navigation-list {
  list-style-type: none;
}

.main__mini-navigation-element {
  margin-bottom: 10px;
}

.main__mini-navigation-content {
  color: rgb(99, 98, 98);
}

.main__mini-navigation-img {
  margin: 20px 0;
  width: 250px;
  height: 150px;
}